import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { ModalSelecionarDiaComponent } from '../modal-components/modal-selecionar-dia/modal-selecionar-dia-component';
import { IonSlides, ModalController, NavController } from '@ionic/angular';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ModalTransferenciaComponent } from '../modal-components/modal-transferencia/modal-transferencia-component';
import { ModalDefaultComponent } from '../modal-components/modal-default/modal-default-component';

@Component({
    selector: 'app-transferir',
    templateUrl: './transferir.page.html',
    styleUrls: ['./transferir.page.scss'],
})

export class TransferirPage implements OnInit {

    listContatos: any[] = [
        {
            id: 1,
            nome: 'Amanda Nunes',
            foto: '/assets/imgs/img-avatar.png'
        },
        {
            id: 2,
            nome: 'Amanda Nunes',
            foto: null
        },
        {
            id: 3,
            nome: 'Amanda Nunes',
            foto: '/assets/imgs/img-avatar.png'
        },
        {
            id: 4,
            nome: 'Amanda Nunes',
            foto: '/assets/imgs/img-avatar.png'
        },
        {
            id: 5,
            nome: 'Amanda Nunes',
            foto: null
        },
    ];

    public formGroup = new FormGroup({
        name: new FormControl('', {
            validators: [
                Validators.required,
            ]
        }),
        institution: new FormControl('', {
            validators: [
                Validators.required,
            ]
        }),
        agency: new FormControl('', {
            validators: [
                Validators.required,
            ]
        }),
        account: new FormControl('', {
            validators: [
                Validators.required,
            ]
        }),
        saveContact: new FormControl(true),
    });

    step = 1;
    saldoAtual = '3.500';
    valorSelecionado = 0;
    contatoSelecionado = null;

    constructor(private router: Router,
                private navCtrl: NavController,
                private modalCtrl: ModalController) {

    }

    async ngOnInit() {
        console.log('ngOnInit - > TransferirPage');
    }

    changeValue(value: any) {
        if (value && value.data) {
            this.valorSelecionado = value.data;
        }
        console.log(this.valorSelecionado);
    }

    stepTo() {
        this.step = this.step + 1;
        this.contatoSelecionado = null;
    }

    onSelectContact(ct) {
        this.contatoSelecionado = ct;
    }

    async openModalConfirmar() {
        let isNew, info;

        if (this.step === 3) {
            isNew = true;
            info = this.formGroup.value;
        } else {
            isNew = false;
            info = this.contatoSelecionado;
        }

        const modal = await this.modalCtrl.create({
                component: ModalTransferenciaComponent,
                keyboardClose: true,
                cssClass: 'modal-fab-bottom',
                componentProps: {
                    data: info,
                    newContact: isNew,
                    valor: this.valorSelecionado
                }
            }
        );

        modal.onWillDismiss().then((data: any) => {
            if (data && data.data) {
                this.openModalSuccess();
            }
        });

        return await modal.present();
    }

    async openModalSuccess() {
        const modal = await this.modalCtrl.create({
                component: ModalDefaultComponent,
                keyboardClose: true,
                cssClass: 'modal-fab-bottom modal-default',
                componentProps: {
                    texto: 'Transferência realizada com sucesso!',
                    icone: '/assets/icons/icon-cash.png',
                    cssClass: 'success',
                }
            }
        );
        modal.onWillDismiss().then((info: any) => {
            this.navCtrl.pop();
        });
        return await modal.present();
    }

    async openModalSelectDay() {
        const modal = await this.modalCtrl.create({
                component: ModalSelecionarDiaComponent,
                keyboardClose: true,
                cssClass: 'modal-fab-bottom',
                componentProps: {}
            }
        );
        // modal.onDidDismiss().then((info: any) => {});
        return await modal.present();
    }
}
